.sidebar{
    flex:0.2;

}
.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
}
.sidebarRow_icon{

color: gray !important;
font-size: large !important;
}

.sidebarRow_title{
flex: 1;
margin-left: 20px;
font-size: 12px;
font-weight: 500;
}

.sidebarRow:hover{
    background-color: lightgray;
    cursor: pointer ;
}

.sidebarRow:hover > .sidebarRow_icon{
    color: rgb(55, 192, 55) !important;
}
.sidebarRow:hover > .sidebarRow_title{
    font-weight: bold;

}
.sidebar > hr{
    height: 1px;
    width: 250px;
    border: 0;
    background-color: lightgrey;
    margin-top: 10px;
    margin-bottom: 10px;
}
