.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 50;
    background-color: white;
}

.header_logo{
   
   height: 25px; 
   object-fit:contain ;
   margin-left: 20px;
}
.header_input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid gray;
}

.header_icons{
    display: flex;
    align-items: center;
}
.header_input > input{
    flex: 1;
    border: none;
}
.input_button{
background-color: #fafafa;
    width: 50px !important;
    border-left: lightgray solid 1px;
    color: gray;
}
.icons{
    margin-right: 8px; 
}